import ShipmentsModel from '../models/shipments';

export default async function updateShipments(shipmentsList) {
  const Shipments = ShipmentsModel();
  await Promise.all(
    shipmentsList.map(async (shipment) => {
      return Shipments.findByIdAndUpdate(
        { _id: shipment._id },
        shipment.update
      );
    })
  );

  return { message: 'Shipments updated successfully' };
}
